.footer {
  background-color: #1b1a21;
  padding: 20px 0;
  text-align: center;
  margin-top: 5em;
}

.footer-container {
  margin: 30px 70px;
  display: flex;
  flex-direction: column;
}

.name {
  text-align: start;
  margin-left: 10px;
  font-weight: bold;
  font-size: 1.1em;
  color: var(--fg-alt);
}

.links-box {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  margin-left: 10px;
  color: white;
}

.footer-link {
  margin-right: 10px;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-link:hover {
  text-decoration: underline;
}


@media (max-width: 600px) {
  .footer {
    padding: 2em;
    margin-top: 3em;
  }
  .footer-container{
    margin: 1em;
  }
  .name{
    margin-bottom: 1em;
  }

}

.footer-link:hover {
  color: #00aaff;
}

.react-icon {
  font-size: 1.2em;
  vertical-align: middle;
}